import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, Epic, Tabbar, TabbarItem, Button, Gallery } from "@vkontakte/vkui";
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Story from '@vkontakte/icons/dist/24/story';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon24Share from '@vkontakte/icons/dist/24/share';
import Icon24Users from '@vkontakte/icons/dist/24/users';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import connect from '@vkontakte/vk-connect';
import { Stack, Page } from "vkui-navigator/dist";

let activeColor = "#2975cc";    

const hello = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader>Acity</PanelHeader>
        <Group>
            <center>
                <Div><img src="https://raw.githubusercontent.com/teth0r/landmarks/master/src/img/welc.png"></img></Div>
                <Div>
                    <Button before={<Icon24Favorite />} onClick={() => connect.send("VKWebAppAddToFavorites", {})} size="xl" level="outline" style={{marginRight: 8}}>Добавить в избранное</Button>
                </Div>
                <Div>
                    <Button before={<Icon24Users />} onClick={() => connect.send("VKWebAppJoinGroup", {"group_id": 187818299})} size="xl" level="outline" style={{marginRight: 8}}>Подписаться на сообщество</Button>
                </Div>
                <Div>
                    <Button before={<Icon24Share />} onClick={() => connect.send("VKWebAppShare", {"link": "https://vk.com/app7177984"})} size="xl" level="outline">Поделиться на стене</Button>
                </Div>

                <Epic>
                    <Tabbar>
                        <TabbarItem text="Главная" onClick={go} data-to="hello" style={{color: activeColor}}>
                            <Icon24Home />
                        </TabbarItem>
                        <TabbarItem text="Города" onClick={go} data-to="dostIndex">
                            <Icon24Story/>
                        </TabbarItem>                           
                    </Tabbar>
                </Epic>                
            </center>
        </Group>
    </Panel>
);

export default hello;